// app/videos/page.jsx
import React from "react";
import VideoCard from "./VideoCard";
import { fetchVideosFromChannel } from "./data";

// Recommended: set channel config here or use env variables
const CHANNEL_HANDLE = process.env.NEXT_PUBLIC_YT_HANDLE || "ModernHealthNutrition";
const CHANNEL_ID = process.env.NEXT_PUBLIC_YT_CHANNEL_ID || null;

export const revalidate = 3600; // ISR: refresh every hour

export default async function VideosPage() {
  let data;
  try {
    data = await fetchVideosFromChannel({ channelId: CHANNEL_ID, handle: CHANNEL_HANDLE, limit: 24 });
  } catch (err) {
    console.error("Videos fetch error", err);
    data = { channelId: null, videos: [] };
  }

  const videos = data.videos ?? [];

  // Grouping suggestion: you can categorize by keywords in title or show 'Latest', 'Hair', 'Skin', etc.
  // For simplicity this example shows Latest first and a simple category strip based on keywords.
  const categories = ["Latest", "Hair", "Skin", "Weight", "Child", "Mental"];
  function findCategory(v) {
    const t = (v.title || "").toLowerCase();
    if (t.includes("hair")) return "Hair";
    if (t.includes("skin")) return "Skin";
    if (t.includes("weight")) return "Weight";
    if (t.includes("child") || t.includes("kid")) return "Child";
    if (t.includes("anxiety") || t.includes("mental")) return "Mental";
    return "Latest";
  }

  // Build map
  const byCategory = {};
  videos.forEach((v) => {
    const c = findCategory(v);
    byCategory[c] = byCategory[c] || [];
    byCategory[c].push(v);
  });

  return (
    <section className="py-20 px-6 md:px-16 lg:px-24 bg-background min-h-screen">
      <div className="max-w-6xl mx-auto">
        <div className="text-center mb-10">
          <h2 className="text-4xl font-bold text-foreground">Video <span className="text-primary">Library</span></h2>
          <p className="text-muted-foreground mt-3 max-w-2xl mx-auto">
            Latest videos from our YouTube channel. Watch treatment explainers, patient stories, and expert talks.
          </p>
        </div>

        {/* Category chips */}
        <div className="flex flex-wrap gap-3 justify-center mb-8">
          {Object.keys(byCategory).map((cat) => (
            <button key={cat} className="px-4 py-2 rounded-full border border-border bg-card text-foreground text-sm">
              {cat} ({byCategory[cat].length})
            </button>
          ))}
        </div>

        {/* Featured / Latest row */}
        <div className="grid md:grid-cols-3 gap-8">
          {videos.slice(0, 6).map((v) => (
            <VideoCard key={v.videoId ?? v.link} video={v} />
          ))}
        </div>

        {/* Sectioned categories (optional) */}
        {Object.keys(byCategory)
          .filter((k) => k !== "Latest")
          .map((cat) => (
            <div key={cat} className="mt-14">
              <div className="flex items-center justify-between mb-6">
                <h3 className="text-2xl font-semibold text-foreground">{cat}</h3>
                <a href={`#${cat}`} className="text-primary text-sm">View all</a>
              </div>

              <div className="grid sm:grid-cols-2 lg:grid-cols-3 gap-8">
                {byCategory[cat].slice(0, 6).map((v) => (
                  <VideoCard key={v.videoId ?? v.link} video={v} />
                ))}
              </div>
            </div>
          ))}
      </div>
    </section>
  );
}
